"use client"

import { useState } from "react"
import QuoteModal from "@/components/quote-modal"

export default function ProductCTABanner() {
  const [isQuoteModalOpen, setIsQuoteModalOpen] = useState(false)
  
  return (
    <section className="relative py-24 md:py-32 bg-slate-900 flex items-center justify-center overflow-hidden">
      {/* Background Image */}
      <div
        className="absolute inset-0"
        style={{
          backgroundImage: "url(/stainless-steel-sheet.png)",
          backgroundSize: "cover",
          backgroundPosition: "center",
        }}
      />
      
      {/* Dark Overlay */}
      <div className="absolute inset-0 bg-slate-900/70"></div>

      {/* Content */}
      <div className="relative z-10 max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
        <h2 className="text-3xl md:text-5xl font-bold text-white mb-6 leading-tight">
          Empower Your Creations with Shanxi Steel's 200 Series: Affordable Strength, Timeless Elegance.
        </h2>
        <p className="text-gray-200 mb-8 text-lg leading-relaxed">
          Discover the perfect balance of performance and value. Contact us today to explore our extensive range of 200 series stainless steel products and elevate your projects.
        </p>
        <button 
          onClick={() => setIsQuoteModalOpen(true)}
          className="bg-blue-600 hover:bg-blue-700 text-white px-8 py-4 rounded font-semibold transition"
        >
          Get a Quote
        </button>
      </div>
      <QuoteModal 
        open={isQuoteModalOpen} 
        onClose={() => setIsQuoteModalOpen(false)}
        source="200 Series CTA"
      />
    </section>
  )
}


